// ********************
// Icons general
//
.link-input{
  background-color: transparent;
  border: none;
  width: 100%;
  line-height: @base-font-size;
  color: @white;
  font-weight: @font-regular;
}

.o-media--centered{
  margin-left: 50%;
  transform: translate(-50%,0);
}
// ********************
// TODO: This should be replaced with +-bubble
  .o-section--new{
    color: @medium-gray;
    background: @lightest-gray;
    width: calc(100% - 2rem);
    margin: 1rem 0 1rem 1rem;
    .rounded-corners-xs;
    .transition(background);
    &:hover{
      background: @light-gray;
      cursor: pointer;
      color: darken(@medium-gray,5);
      .o-icon--light-gray{
        fill: darken(@light-gray,20);
      }
    }
  }
// ********************
// FORM
.input-field{
  padding-top: 14px;
  & label{
    position: absolute;
    top: 0;
    .font-size(small);
  }
  & input{
    .font-size(base);
  }
  input[value=""]{
    background:red;
  }
}
